import axios from 'axios';
import HTMLEmailTemplate from '../variables/HTMLEmailTemplate';
import KatalonEndpoints from '../variables/KatalonEndpoints';

export default class KSEmailTemplateService {
    static async fetchEmailTemplate(type, isSetDefaults) {
        let data = await axios.get(`${KatalonEndpoints.HTMLEmailTemplate}?type=${type}&setDefaults=${isSetDefaults}`);
        return data.data;
    }

    static async fetchTestSuiteEmailTemplate(isSetDefaults) {
        return this.fetchEmailTemplate(HTMLEmailTemplate.TEST_SUITE, isSetDefaults);
    }

    static async fetchTestSuiteCollectionEmailTemplate(isSetDefaults) {
        return this.fetchEmailTemplate(HTMLEmailTemplate.TEST_SUITE_COLLECTION, isSetDefaults);
    }
}