import React from "react";
import HTMLEmailTemplate from "../../variables/HTMLEmailTemplate";
import KSEmailTemplateService from "../../services/KSEmailTemplateService";
import { Skeleton } from "@material-ui/lab";
import { Editor } from "@tinymce/tinymce-react";

export default function EmailTemplatePage() {
  const [content, setContent] = React.useState();
  const url = new URLSearchParams(window.location.search);
  const type = url.get("type");
  const isSetDefaults = url.get("setDefaults") ?? false;

  const fetchEmailContentTemplate = async () => {
    if (type === HTMLEmailTemplate.TEST_SUITE) {
      await KSEmailTemplateService.fetchTestSuiteEmailTemplate(
        isSetDefaults
      ).then((data) => {
        setContent(String(data));
      });
    }
    if (type === HTMLEmailTemplate.TEST_SUITE_COLLECTION) {
      await KSEmailTemplateService.fetchTestSuiteCollectionEmailTemplate(
        isSetDefaults
      ).then((data) => {
        setContent(String(data));
      });
    }
  };

  React.useEffect(() => {
    setTimeout(() => {
      fetchEmailContentTemplate();
    }, 1000);
    // Fix Promise.allSettled is not a function when opened in an embedded browser in KS.
    Promise.allSettled =
      Promise.allSettled ||
      ((promises) =>
        Promise.all(
          promises.map((p) =>
            p
              .then((value) => ({
                status: "fulfilled",
                value,
              }))
              .catch((reason) => ({
                status: "rejected",
                reason,
              }))
          )
        ));
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <>
      {content ? (
        <Editor
          tinymceScriptSrc={process.env.PUBLIC_URL + "/tinymce/tinymce.min.js"}
          initialValue={content}
          init={{
            menubar: false,
            statusbar: false,
            plugins: [
              "advlist",
              "autolink",
              "lists",
              "link",
              "image",
              "charmap",
              "anchor",
              "searchreplace",
              "visualblocks",
              "code",
              "fullscreen",
              "media",
              "table",
            ],
            toolbar:
              "undo redo | blocks | " +
              "bold italic forecolor | alignleft aligncenter " +
              "alignright alignjustify | bullist numlist outdent indent | " +
              "removeformat",
            content_style: "body { font-family:Helvetica,Arial,sans-serif }",
            paste_data_images: false,
            setup: function (editor) {
              editor.on("init", function (e) {
                editor.execCommand("mceFullScreen");
              });
            },
          }}
        />
      ) : (
        <Skeleton />
      )}
    </>
  );
}
